@mixin para-tablets-1000 {
    @media screen and (max-width: 1000px) {
        @content;
    }
}

@mixin para-tablets-900 {
    @media screen and (max-width: 900px) {
        @content;
    }
}


@mixin para-tablets-850 {
    @media screen and (max-width: 850px) {
        @content;
    }
}


@mixin para-tablets-800 {
    @media screen and (max-width: 800px) {
        @content;
    }
}

@mixin para-tablets-600 {
    @media screen and (max-width: 600px) {
        @content;
    }
}

@mixin para-movil {
    @media screen and (max-width: 450px) {
        @content;
    }
}



@mixin para-movil-horizontal {
    @media screen and (max-width: 1000px) and (max-height: 500px) and (orientation: landscape) {
        @content;
    }
}

